@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600&display=swap);
body {
  margin: 0;
  font-family: 'Quicksand', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  padding: 10px 15%;
  color: #424242;
  font-size: 15px;
  justify-content: center;
}

a {
  text-decoration: none;
  color: #424242;
}
.text-navigation {
    width: 40%;
}
.text-navigation ul {
    display: flex;
    padding: 0px;
    justify-content: space-between;
}

.nav-item {
    list-style: none;
    margin: 0px 10px;
}

.text-navigation a {
    font-size: 15px;
    font-weight: 600;
    padding-bottom: 5px;
    border-bottom: 2px solid transparent;
}

.text-navigation a:hover,
.nav-item--active a {
    color: #6DCD82;
    border-bottom: 2px solid #6DCD82;
}
.icon-navigation {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: flex;
    margin-top: 10px;
    text-align: right;
}

.icon-navigation div {
    margin-left: 20px;
}

.icon-navigation img {
    width: 18px;
    height: auto;
}

.icon--active,
.icon-navigation img:hover {
    -webkit-filter: invert(73%) sepia(57%) saturate(341%) hue-rotate(80deg) brightness(90%) contrast(87%);
            filter: invert(73%) sepia(57%) saturate(341%) hue-rotate(80deg) brightness(90%) contrast(87%);
}
.cart-dropdown {
    position: absolute;
    top: 60px;
    right: 18%;
    border: solid grey 1px;
    width: 200px;
    padding: 20px;
    background-color: white;
}

.cart-dropdown ul {
    padding: 0;
    margin: 0;
}

.dropdown__item {
    list-style: none;
    display: flex;
    margin-bottom: 15px;
}

.dropdown__item img {
    width: 80px;
    height: auto;
    margin-right: 20px;
}

.item__info {
    display: flex;
    flex-direction: column;
    font-size: 13px;
    justify-content: center;
}

.item__info span {
    width: 100%;
}

.item__name {
    font-weight: 500;
}

.cart-dropdown .custom-btn {
    width: 100%;
}

.empty-message {
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.custom-btn {
    background-color: #6DCD82;
    border: 1px solid white;
    height: 45px;
    padding: 0 20px;
    color: white;
    font-weight: 600;
    transition: all 0.2s;
}

.custom-btn:focus {
    outline: none;
}

.btn--standard:hover {
    background-color: white;
    border: 1px solid #6DCD82;
    color: #6DCD82;
}

.btn--white {
    font-weight: 300;
    text-transform: uppercase;
    background-color: white;
    border: 1px solid black;
    color: black;
    opacity: 0.7;
    visibility: hidden;
    transition: visibility 0.2s;
}

.btn-google {
    background-color: #4285F4;
}

.btn-google:hover {
    background-color:white;
    border: 1px solid  #4285F4;
    color:  #4285F4;
}

.btn-facebook {
    background-color: #4267B2;
}

.btn-facebook:hover {
    background-color:white;
    border: 1px solid  #4267B2;
    color:  #4267B2
}
.header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
}

.header__branding {
    display: flex;
    margin: 7px 0;
}

.header__branding img {
    width: 25px;
    height: 25px;
    margin-right: 8px;
}

.header__branding h2 {
    color: #6DCD82;
    margin: 0;
}

.homepage {
    display: flex;
    flex-direction: column;
}
.section {
    display: flex;
    margin-bottom: 40px;
}

.section__img {
    width: 400px;
    height: auto;
    margin: 0 30px 0 0;;
}

.section__info span {
    font-size: 15px;
}

.section__info .custom-btn {
    margin-top: 15px;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    width: 40%;
}

.end-with-pic img {
    order: 2;
    margin: 0 0 0 30px;
}

.end-with-pic .section__info {
    text-align: right;
}
.shop-item {
    margin: 10px 20px 10px  0;
    display: flex;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    flex-direction: column;
}

.item__img {
    width: 280px;
    height: 350;
    object-fit: cover;
}

.shop-item .custom-btn {
    width: 80%;
    align-self: center;
    position: relative;
    top: -70px;
    
}

.item__details {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: -35px;
}

.item__name {
    width: 80%;
}
.item__price {
    width: 20%;
    text-align: right;
}

.shop-item:hover .custom-btn {
    visibility: visible;
}

.shoppage h2 {
    font-weight: 500;
    font-size: 15px;
}

.shoppage__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    padding: 0;
    margin: 0;
}
.care-container {
    margin-bottom: 30px;
}

.care__item {
    display: flex;
    text-align: center;
    margin: 20px 0;
}

.care__item img {
    width: 20px;
    height: 20px;
    margin-right: 10px;
}

.care__item span {
    text-align: left;
    width: 200px;
    border-bottom: 2px solid #6DCD82;
    padding-bottom: 8px;
}
.productpage {
    display: flex;
    flex-direction: column;
}

.to-category {
    display: flex;
    text-align: center;
    margin-bottom: 10px;
    font-size: 13px;
}

.to-category img {
    width: 15px;
    height: auto;
    margin-right: 3px;
}

.product-container {
    display: flex;
}

.product__col--1 {
    margin-right: 30px;
}

.product__col--1 img {
    width: 400px;
    height: auto;
}

.product__col--2 h2 {
    margin: 0 0 30px 0;
}

.product__col--2 p {
    margin: 0 0 30px 0;
}
.contactpage {
    display: flex;
    justify-content: center;
    padding-top: 20px;
}

.contact-details {
    display: flex;
    flex-direction: column;
    margin-left: 50px;
}

.location, .online {
    display: flex;
    flex-direction: column;
    line-height: 25px;
    padding: 15px 0;
}

.location {
    border-bottom: 2px solid #6DCD82;
}

.contactpage h3 {
    margin-top: 0;
}

.social img {
    width: 25px;
    height: 25px;
    margin-right: 20px;
}
.checkout__heading {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    padding-bottom: 10px;
    border-bottom: 1px solid #6DCD82;
}

.checkout__heading span {
    text-align: center;
    color: #6DCD82;
    font-size: 17px;
    font-weight: 500;
}

.checkout__container {
    text-align: right;
}

.container__list {
    list-style: none;
    padding: 0;
    border-bottom: 1px solid #424242;
    margin-bottom: 15px;
}

.checkout__container .custom-btn {
    width: 200px;
}

.empty-checkout-msg {
    display: flex;
    width: 100%;
    height: 200px;
    justify-content: center;
    align-items: center;
}
.checkout-item {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    align-items: center;
    text-align: center;
    margin: 15px 0;
}

.item__desc {
    display: flex;
    flex-direction: column;
}

.item__desc span {
    text-align: left;
}

.item__desc img {
    width: 200px;
    height: auto;
    margin-bottom: 10px;
}

.item__quantity {
    display: flex;
    justify-content: center;
}

.item__quantity .clear-icon {
    width: 18px;
    height: auto;
}

.item__quantity .update-icon {
    position: relative;
    width: 15px;
    height: auto;
    margin: 0 15px;
    top: 3px;
}

.item__quantity input {
    width: 50px;
}

.order-summary {
    font-weight: 600;
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
}
.social-acct-signinup {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 1px solid #6DCD82;
    width: 350px;
    padding-bottom: 20px;
    margin: 0 auto;
}

.social-acct-signinup .custom-btn {
    width: 220px;
    margin: 5px auto;
}
.sign-in-up-email {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sign-in-up-email h2 {
    font-size: 18px;
}

.sign-in-up-email .custom-btn {
    width: 220px;
    margin: 20px auto 10px auto;
}

.email-form {
    display: flex;
    flex-direction: column;
    text-align: center;
}

.change-sign-up-in-btn {
    font-size: 12px;
    text-decoration: underline;
    margin: 5px 0;
}
.form-input {
    display: flex;
    margin: 10px 0;
}

.input__label {
    width: 120px;
    font-size: 15px;
}

.input__error {
    display: flex;
    flex-direction: column;
}

.form-input input {
    width: 250px;
    padding-bottom: 5px;
    border: none;
    border-bottom: 1px solid #424242;
    text-align: center;
}

.form-input input:focus {
    outline-style: none;
}

.validation-error {
    font-size: 10px;
    margin-top: 5px;
    color: red;
}
.welcomepage {
    display: flex;
    flex-direction: column;
}
.orderpage {
    display: flex;
    justify-content: center;
}

.orderpage h2 {
    font-size: 20px;
    margin: 0 0 30px 0;
}

/* ---------------------------------- */
/* Shipping and Payment Section       */
/* ---------------------------------- */
.orderpage .shipping-address,
.payment-info {
    margin-bottom: 50px;
}

.shipping-address input {
    text-align: left;
}

.test-credit-card {
    border: 1px solid red;
    padding: 15px;
    margin-bottom: 20px;
}

.payment-info {
    width: 380px;
}

/* ---------------------------------- */
/* Purchase Order Summary             */
/* ---------------------------------- */
.summary-section {
    border: 1px solid #6DCD82;
    padding: 18px 35px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-left: 80px;
}

.orderpage .order-summary span {
    margin: 5px 0;
    text-align: right;
    font-weight: 400;
}

.orderpage .custom-btn {
    width: 100%;
}
.address__province {
    display: flex;
    margin: 25px 0;
}

.address__province label {
    width: 120px;
    font-size: 15px;
}

.address__province select {
    width: 250px;
}

.address__province select:focus {
    outline: none;
}
